<style>
@import "./editor.css";
@import "./codehighlight.css";
</style>


<template>
  <editor-content id="container" :editor="editor" />
</template>

        
<script>
import { Editor, EditorContent } from "tiptap";
import Doc from "./customDoc";
import Title from "./title";

import CodeBlockHighlight from "./codeBlockHighlight";
import TipTapCustomImage from "./TipTapImage";
import OrderedList from "./OrderedList";
import BulletList from "./BulletList";
import TodoItem from "./TodoItem";
import TextColor from "./TextColor";

// Enable smooth scrolling on safari iOS
import "scroll-behavior-polyfill";

function scrollIntoView(t) {
  if (typeof t != "object") return;

  if (t.getRangeAt) {
    // we have a Selection object
    if (t.rangeCount == 0) return;
    t = t.getRangeAt(0);
  }

  if (t.cloneRange) {
    // we have a Range object
    var r = t.cloneRange(); // do not modify the source range
    r.collapse(true); // collapse to start
    t = r.startContainer;
    // if start is an element, then startOffset is the child number
    // in which the range starts
    if (t.nodeType == 1) t = t.childNodes[r.startOffset];
  }

  // if t is not an element node, then we need to skip back until we find the
  // previous element with which we can call scrollIntoView()
  var o = t;
  while (o && o.nodeType != 1) o = o.previousSibling;
  t = o || t.parentNode;
  if (t)
    t.scrollIntoView({
      behavior: "smooth",
      block: "nearest",
    });
}

window.scrollToSelection = () => {
  scrollIntoView(window.getSelection());
};

import {
  Blockquote,
  Heading,
  ListItem,
  HardBreak,
  TodoList,
  Bold,
  Code,
  Italic,
  Link,
  Strike,
  Underline,
  History,
  Search,
  Placeholder,
  TrailingNode,
} from "tiptap-extensions";

// Highlight.js imports
import javascript from "highlight.js/lib/languages/javascript";
import css from "highlight.js/lib/languages/css";
import swift from "highlight.js/lib/languages/swift";
import rust from "highlight.js/lib/languages/rust";
import python from "highlight.js/lib/languages/python";

window.editor = new Editor({
  extensions: [
    new Doc(),
    new Title(),
    new Blockquote(),
    new BulletList(),
    new CodeBlockHighlight({
      languages: {
        javascript,
        css,
        swift,
        rust,
        python,
      },
    }),
    new HardBreak(),
    new Heading({ levels: [1, 2, 3] }),
    new ListItem(),
    new OrderedList(),
    new TodoItem({
      nested: true,
    }),
    new TodoList(),
    new Link(),
    new Bold(),
    new Code(),
    new Italic(),
    new Strike(),
    new Underline(),
    new History(),
    new Search(),
    new Placeholder({
      showOnlyCurrent: false,
      emptyNodeText: (node) => {
        if (node.type.name === "title") {
          return "Untitled";
        }
        return "Write something here...";
      },
    }),
    new TrailingNode({
      node: "paragraph",
      notAfter: ["paragraph"],
    }),
    new TipTapCustomImage(),
    new TextColor(),
  ],
  autoFocus: true,
  onTransaction: () => {
    var isActive = window.editor.isActive;

    try {
      var format = {
        bold: isActive.bold(),
        italic: isActive.italic(),
        underline: isActive.underline(),
        strike: isActive.strike(),
        heading: isActive.heading({ level: 1 })
          ? 1
          : isActive.heading({ level: 2 })
          ? 2
          : isActive.heading({ level: 3 })
          ? 3
          : 0,
        todo_list: isActive.todo_list(),
        ordered_list: isActive.ordered_list(),
        bullet_list: isActive.bullet_list(),
        sink_list: window.editor.commands.can_sink_list(),
        lift_list: window.editor.commands.can_lift_list(),
        code_block: isActive.code_block(),
        blockquote: isActive.blockquote(),
        text_color: window.editor.activeMarkAttrs.text_color["color"],
      };
    } catch (err) {
      console.log(err);
    }
    try {
      window.webkit.messageHandlers.formatChange.postMessage({
        format: format,
      });
    } catch {
      console.log("Couldn't send format change event to WebKit");
      console.log(format);
    }
  },
  onUpdate: (data) => {
    let html = data.getHTML();
    try {
      window.webkit.messageHandlers.textChange.postMessage({
        html: html,
      });
    } catch {
      console.log("Couldn't send textChange event to WebKit");
      console.log(html);
    }
  },
});

export default {
  name: "TextEditor",
  components: {
    EditorContent,
  },
  data() {
    return {
      editor: window.editor,
    };
  },
  beforeDestroy() {
    this.editor.destroy();
  },
};
</script>